<template>
  <v-app id="app">
    <v-navigation-drawer v-model="drawer" app temporary>
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title>
            <span class="red--text text--darken-1">LPK</span>
            <span>AISHIN INDONESIA</span>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
      <v-list dense nav >
        <v-list-item-group active-class="blue--text text--accent-4">
          <v-list-item @click="setLastPage('/lpkmember')">
            <v-list-item-icon>
              <v-icon color="red">mdi-account-details-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-title>List LPK Member</v-list-item-title>
          </v-list-item>
          <v-list-item @click="setLastPage('/addmember')">
            <v-list-item-icon>
              <v-icon color="red">mdi-account-multiple-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Add LPK Member</v-list-item-title>
          </v-list-item>
          <v-list-item @click="setLastPage('/regclient')">
            <v-list-item-icon>
              <v-icon color="red">mdi-account-multiple-plus-outline</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Register New Client</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar app color="red darken-1" dense>
  <v-app-bar-nav-icon @click="drawer = !drawer" class="white--text text--darken-1"></v-app-bar-nav-icon>

  <v-toolbar-title id="toolbar-title">
    <span class="white--text text--darken-1">LPK </span>
    <span class="white--text text--darken-1">AISHIN GROUP</span>
  </v-toolbar-title>

  <!-- Tambahkan spacer agar LOGOUT berada di sisi kanan -->
  <v-spacer></v-spacer>

  <v-menu bottom min-width="200px" offset-y>
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="red" depressed rounded small text @click="logout()" v-bind="attrs" v-on="on">
        <v-icon color="white">mdi-logout</v-icon>
        <span class="white--text">LOGOUT</span>
      </v-btn>
    </template>
  </v-menu>
</v-app-bar>


    <v-main>
      <router-view></router-view>
    </v-main>

    <ConfirmationDialog ref="confirmationDialog"/>
    <DeleteDialog ref="deleteDialog"/>
    <InformationDialog ref="informationDialog"/>
    <LoadingDialog ref="loadingDialog"/>
    <Snackbar ref="snackbar"/>
  </v-app>
</template>

<script>
// import {useRoute} from "vue-router/composables";

export default {
  name: "AppLayout",
  data() {
    return {
      drawer: null,
      identity: null,
    };
  },

  async mounted() {
    // this.identity = this.storageLoad("auth/identity");
    // console.log(this.identity)
    // if(useRoute().path.slice(1) !== "login") {
    //   if (!this.identity) {
    //     await this.$router.push("/login");
    //   }
    // }
    // else{
    //   if (!this.identity) {
    //     await this.$router.push("/login");
    //   }
    //   // else {
    //   //   let lastPage = this.storageLoad("cache/lastPage");
    //   //   console.log(`pindah ke ${lastPage}`)
    //   //   await this.$router.push(lastPage)
    //   // }
    // }
  },

  methods: {
    setLastPage(href) {
      this.storageSave("cache/lastPage", href.replace("/#",""));
      this.$router.push(href)
    },
    async logout() {
      const res = await this.$refs.confirmationDialog.show(
          "Exit application?",
          "Logout"
      );
      if (res) {
        this.storageRemove("auth/identity");
        var tesAuth = this.storageLoad("auth/identity");
        this.toast(tesAuth)
        // this.$store.commit("auth/identity", {});
        this.toast("Logout success", "success");
        // localStorage.setItem("auth/identity", "")
        // localStorage.removeItem("auth/identity")
        await this.$router.push("/login");
        this.$router.go(0);
      }
    },
  },
};
</script>
