<template>
  <v-app id="login" class="login-bg">
    <v-app-bar app color="teal" dark>
      <v-btn icon @click="goHome">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>

      <v-spacer></v-spacer>
      <v-toolbar-title>PT. AISHIN INDONESIA</v-toolbar-title>
      <v-spacer></v-spacer>
      <!-- Isi tambahan untuk navbar jika diperlukan -->
    </v-app-bar>
    <v-main>
      <v-container class="d-flex align-items-center justify-content-center" fill-height fluid>

        <v-row dense justify="center">
          <v-col cols="12" sm="8" md="4">
            <v-card class="login-box shadow">
              <v-card-title class="login-box__title">Login</v-card-title>
              <v-card-text>
                <v-form>
                  <div>
                    <v-text-field id="username" v-model="resource.username" autocomplete="off" class="mb-4" dense
                      hide-details label="Username" prepend-icon="mdi-account" outlined></v-text-field>

                    <v-text-field id="password" v-model="resource.password"
                      :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'" :type="showPassword ? 'text' : 'password'"
                      autocomplete="off" class="mb-5" dense hide-details label="Password" prepend-icon="mdi-lock" outlined
                      @click:append="showPassword = !showPassword" @keydown.enter="login()"></v-text-field>

                    <v-btn :disabled="loading" :loading="loading" block class="white--text" color="red darken-3"
                      @click="login()">Login
                    </v-btn>
                  </div>
                </v-form>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
    <v-footer color="teal" app padless>
      <v-col class="white--text text-center">
         {{ new Date().getFullYear() }} PT. AISHIN INDONESIA
      </v-col>
    </v-footer>
  </v-app>
</template>

<script>
import { mapGetters } from "vuex";
import authApi from "../api/AuthApi";
export default {
  name: "Login",

  data() {
    return {
      resource: {
        username: "",
        password: "",
      },
      version: "v1.0.10",
      showPassword: false,
      loading: false,
      errorMessage: "",
      dialogSetting: false,
      tempBaseURL: null,
      debugDownload: false,
      db: null,
    };
  },
  computed: {
    ...mapGetters({
      baseURL: 'auth/baseURL',
      lastCache: 'base/lastCache',
      listCache: 'base/listCache'
    }),
  },
  async mounted() {
  },

  methods:
  {
    async login() {
      try {
        const identity = await authApi.login(this.resource);
        this.storageSave("auth/identity", identity);
        await this.$router.push("/client/profile");
      } catch (error) {
        this.toast(error.message)
      }
    },
    goHome() {
      this.$router.push('/ja/home'); // Navigasi ke halaman utama ("/")
    },
  },
}
</script>

<style scoped>
#login {
  background: url('/images/login.jpg') no-repeat center center fixed;
  background-size: 50% 80%;
}


.centered-input>>>input {
  text-align: center;
}
.login-box__title {
  display: block;
  text-align: center;
}

</style>
