<template>
  <v-app>
    <!-- Navbar/Header -->
    <v-app-bar app color="red" dark>
      <v-btn icon @click="goBack">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
      <v-spacer></v-spacer>
      <v-toolbar-title>Create New Member LPK</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-app-bar>

    <!-- Konten Utama -->
    <v-main>
      <v-container>
        <v-row>
          <v-col cols="12">
            <v-card class="mt-4 mb-4 pa-5">
              <v-spacer></v-spacer>

              <v-form @submit.prevent="submitForm">
          <!-- Nama -->
          <v-row>
            <v-col cols="12">
              <v-text-field outlined v-model="newItem.name" label="Nama"></v-text-field>
            </v-col>
          </v-row>
          <!-- Tempat Lahir -->
          <v-row>
            <v-col cols="4">
              <v-text-field outlined v-model="newItem.birthPlace" label="Tempat Lahir"></v-text-field>
            </v-col>
            <v-col cols="4">
              <v-text-field outlined v-model="newItem.birthDay" label="Tanggal Lahir" type="date"></v-text-field>
            </v-col>
            <v-col cols="4">
              <v-select outlined v-model="newItem.gender" :items="genderOptions" label="Jenis Kelamin"></v-select>
            </v-col>
          </v-row>

          <!-- Agama -->
          <v-row>
            <v-col cols="4">
              <v-select outlined v-model="newItem.religion" :items="religionOptions" label="Agama"></v-select>
            </v-col>
            <v-col cols="4">
              <v-text-field outlined v-model="newItem.phone" label="Nomor Telepon"></v-text-field>
            </v-col>
            <v-col cols="4">
              <v-text-field outlined v-model="newItem.email" label="Email"></v-text-field>
            </v-col>
            </v-row>
            <v-row>
            <v-col cols="12">
              <v-text-field outlined v-model="newItem.address" label="Alamat"></v-text-field>
            </v-col>
          </v-row>

          <!-- Pendidikan Terakhir -->
          <v-row>
            <v-col cols="12">
              <v-select outlined v-model="newItem.education" :items="educationOptions" label="Pendidikan Terakhir"></v-select>
            </v-col>
          </v-row>

          <!-- Nama Perusahaan di Jepang -->
          <v-row>
            <v-col cols="12">
              <v-text-field outlined v-model="newItem.companyNameJapan" label="Nama Perusahaan di Jepang"></v-text-field>
            </v-col>
          </v-row>

          <!-- Alamat Perusahaan di Jepang -->
          <v-row>
            <v-col cols="12">
              <v-text-field outlined v-model="newItem.companyAddressJapan" label="Alamat Perusahaan di Jepang"></v-text-field>
            </v-col>
          </v-row>

          <!-- Tanggal Pergi ke Jepang -->
          <v-row>
            <v-col cols="12">
              <v-text-field outlined v-model="newItem.dateToJapan" label="Tanggal Pergi ke Jepang" type="date"></v-text-field>
            </v-col>
          </v-row>

          <!-- Sertifikat JLPT -->
          <v-row>
            <v-col cols="12">
              <v-select outlined v-model="newItem.jlptCertificate" :items="jlptOptions" label="Sertifikat JLPT"></v-select>
            </v-col>
          </v-row>

          <!-- Skills -->
          <v-row>
            <v-col cols="12">
              <v-text-field outlined v-model="newItem.skills" label="Skills"></v-text-field>
            </v-col>
          </v-row>

          <!-- LPK Asal -->
          <v-row>
            <v-col cols="12">
              <v-text-field outlined v-model="newItem.LPKOrigin" label="LPK Asal"></v-text-field>
            </v-col>
          </v-row>

          <!-- Tanggal Bergabung dengan LPK -->
          <v-row>
            <v-col cols="12">
              <v-text-field outlined v-model="newItem.DateJoinLPK" label="Tanggal Bergabung dengan LPK" type="date"></v-text-field>
            </v-col>
          </v-row>

          <!-- Upload Foto -->
          <v-row>
            <v-col cols="12">
              <v-file-input outlined label="Upload Foto"></v-file-input>
            </v-col>
          </v-row>

          <!-- Tombol Submit -->
          <v-row>
            <v-col cols="12">
              <v-btn type="submit" color="primary">Tambah Member</v-btn>
            </v-col>
          </v-row>
        </v-form>
        </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>

    <!-- Footer -->
    <v-footer color="black" app padless>
      <v-col class="white--text text-center">
        Copyright by PT. AISHIN  {{ new Date().getFullYear() }}
      </v-col>
    </v-footer>
  </v-app>
</template>

<script>
import employee  from "../../api/EmployeeApi";

export default {
  data() {
    return {
      newItem: {
        name: '',
        birthPlace: '',
        birthDay: null,
        gender: '',
        religion: '',
        phone: '',
        email: '',
        address: '',
        education: '',
        companyNameJapan: '',
        companyAddressJapan: '',
        dateToJapan: null,
        jlptCertificate: '',
        skills: '',
        LPKOrigin: '',
        DateJoinLPK: null,
      },
      genderOptions: ['Laki-laki', 'Perempuan'],
      religionOptions: ['Islam', 'Kristen', 'Katholik', 'Hindu', 'Buddha', 'Konghucu', 'Atheis', 'Others'],
      jlptOptions: ['N5', 'N4', 'N3', 'N2', 'N1'],
      educationOptions: ['SD', 'SMP', 'SMA', 'Diploma', 'S1', 'S2', 'S3'],
    };
  },
  methods: {

    goBack() {
      this.$router.go(-1); // Atau logika navigasi lainnya
    },

    async submitForm() {
      this.mode = 'create';
      await this.save();
    },
    async save() {
      try {
        // const res = await this.$refs.confirmationDialog.show(
        //   "Save data?",
        //   "Confirmation"
        // );
        // if (res) {


          if (this.mode === "create") {
            let result = await employee.create(this.newItem);
            if (result.ErrorMessage) {
              throw new TypeError(result.ErrorMessage)
            }
            this.resource = {}
          } else if (this.mode === "update")
            await employee.update(this.newItem);

          await this.$refs.informationDialog.show(
            "Data saved successfully.",
          );
          await this.$router.push("/");
        // }
      } catch (error) {
        this.$refs.snackbar.showError(error);
      }
    },

  }
};
</script>

<style scoped>
  .v-footer {
    color: white;
  }

  /* Style untuk v-card */
  .v-card {
    margin-top: 1cm;
    margin-bottom: 1cm;
  }
</style>