<template>
    <v-app>
    <!-- Navbar/Header -->
    <v-app-bar app color="red" dark>
      <v-btn icon @click="goBack">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
      <v-spacer></v-spacer>
      <v-toolbar-title>List Member LPK</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-main>
  <v-container fluid>
    <!-- Tabel Daftar Member -->
    <v-card>
      <v-data-table :headers="headers" :items="members" :items-per-page="10" :search="search" class="elevation-1">
        <template v-slot:item.action="{ item }">
          <v-btn icon @click="viewProfile(item)">
            <v-icon>mdi-eye</v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>

    <!-- Dialog/Pop-up untuk View Profile -->
    <v-dialog v-model="dialog" max-width="600px">
      <v-card>
        <v-card-title class="headline">{{ selectedMember.name }}</v-card-title>
        <v-card-text>
          <!-- Tampilkan detail profil di sini -->
          <!-- ... -->
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="dialog = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</v-main>

<!-- Footer -->
<v-footer color="black" app padless>
  <v-col class="white--text text-center">
     {{ new Date().getFullYear() }} Your Company
  </v-col>
</v-footer>
</v-app>
</template>

<script>
import employeeApi from "../../api/EmployeeApi";

export default {
  data() {
    return {
      members: [],
      dialog: false,
      selectedMember: {},
      search: '',
      headers: [
        { text: 'Name', value: 'name' },
        { text: 'BirthDay', value: 'birthDay' },
        { text: 'Gender', value: 'gender' },
        { text: 'Religion', value: 'religion' },
        { text: 'JLPT Certificate', value: 'jlptCertificate' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1); // Atau logika navigasi lainnya
    },
    async loadData() {
      try {
        const response = await employeeApi.getAll();
        this.members = response.data;
      } catch (error) {
        console.error(error);
        // Handle error
      }
    },
    viewProfile(member) {
      this.selectedMember = member;
      this.dialog = true;
    },
  },
  mounted() {
    this.loadData();
  }
};
</script>
<style scoped>
  .v-footer {
    color: white;
  }

  .v-card {
    margin-top: 1cm;
    margin-bottom: 1cm;
  }
</style>