<template>
  <v-container fluid>
    <h2 class="text-center my-4 custom-h2">CONTACT US</h2>
    <v-row justify="center">
      <v-col cols="12" md="6">
        <v-form @submit.prevent="sendEmail">
          <v-text-field v-model="form.name" label="Name" outlined color="red" required></v-text-field>
          <v-text-field v-model="form.email" label="Email" outlined color="red" required type="email"></v-text-field>
          <v-textarea v-model="form.message" label="Message" outlined color="red" required></v-textarea>
          <v-btn type="submit" large block color="red">Send</v-btn>
        </v-form>
      </v-col>
    </v-row>

    <v-container fluid>
      <!-- Margin top class added here for spacing from the content above -->
      <v-card flat class="mt-5">
        <v-row no-gutters justify="center">
          <v-col cols="12" md="4" class="d-flex justify-center align-center">
            <!-- Brand Logo and Description -->
            <v-row>
              <v-col class="d-flex justify-center justify-md-start py-3">
                <img src="/images/logo-bordered.png" alt="AISHIN Group Logo" height="150">
                <!-- Add your logo image here -->
              </v-col>
            </v-row>
            <v-row>
              <v-col class="d-flex justify-center justify-md-start py-3">
                <!-- Logos for certifications or partners can go here -->
              </v-col>
            </v-row>
          </v-col>
          <v-col cols="6" md="2">
            <!-- Pages Section -->
            <v-card flat>
              <v-card-text class="large-text">
                <div class="footer-heading">Pages</div>
                <v-list dense>
                  <v-list-item href="/en-us/home">Home</v-list-item>
                  <v-list-item href="/en-us/member">Our Members</v-list-item>
                  <v-list-item href="/en-us/about">About Us</v-list-item>
                  <v-list-item href="/en-us/so">Sending Organization</v-list-item>
                  <v-list-item href="/en-us/activity">Educational Activities</v-list-item>
                  <v-list-item href="/en-us/contact">Contact</v-list-item>
                  <v-list-item href="/en-us/brocure">Brochure</v-list-item>
                </v-list>
              </v-card-text>
            </v-card>
          </v-col>

          <v-col cols="12" md="3">
            <!-- Address Section -->
            <v-card flat>
              <v-card-text class="large-text">
                <div class="footer-heading">Address</div>
                <div>Jl. Terusan I Gusti Ngurah Rai No.1, Pd. Kopi, Kec. Duren Sawit, East Jakarta City, Special Capital
                  Region of Jakarta 13460</div>
                <div>Phone: (021) 8974540</div>
                <div>Email: info@aishingroup.co.id</div>
                <div>Business Hours: 08:00 - 17:00</div>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-card>
    </v-container>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: '',
        email: '',
        message: ''
      },
      // ... other data properties ...
    };
  },
  methods: {
    async sendEmail() {
      try {
        // Here you would send the form data to your server-side script.
        // For demonstration, I'm using a placeholder URL.
        const response = await fetch('/api/send-email', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(this.form)
        });

        if (response.ok) {
          // Handle success
          alert('Email sent successfully!');
        } else {
          // Handle error
          alert('Failed to send email.');
        }
      } catch (error) {
        console.error('There was an error sending the email:', error);
      }
    }
  }
};
</script>

